form {
    background-color: white;
    display: flex;
    flex-direction: column;
    width: 70%;
    align-items: center;
    padding: 20px;
}

.titulo {
    color: black;
    font-weight: bold;
    text-align: center;
    margin-bottom: 20px;
}

.texto {
    background-color: #F9FBFC;
    border: 2px solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
}

.texto::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
}

.texto:focus {
    outline-color: #61DAFA;
}

.contenedor-botones {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.boton-form {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat';
}

.boton-cancelar {
    background: transparent;
}

.boton-anadir {
    background: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
}